<template>
	<div>
		<div class="kd-page-title">
			手机修改
		</div>
		<div class="kd-page-content-wrapper wrap-info">
			<verify-code
					@getVerifyCode="getVerifyCode"
					ref="verifyCode"
					:change-phone-business="true"
					:only-need-phone="checkNeedVerify()"
			/>
		</div>
		<div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
			<kd-button
					icon="kedaIcon-policework"
					text="返回首页"
					type-class="home"
			/>
			<kd-button
					icon="kedaIcon-circleleft"
					text="上一步"
					:button-handle="goUp"
			/>
			<kd-button
					icon="kedaIcon-duihao2"
					text="提交"
					:button-handle="submission"
			/>
		</div>
		<alert-msg
				v-if="alertOn"
				:alertSure="sure"
				:alertCancel="cancel"
				:tip-text="tipText"
		/>
	</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { driverLicenseGetVerifyCode, driverLicenseVerifyCode, driverLicenseSubModify } from '@/service/traffic/traffic';
import alertMsg from '@/components/alert-msg';
import verifyCode from '@/components/verify-code';
import { checkNeedVerify } from '@/utils/regional';

export default {
    name: 'DriverContactCheckPhone',
    components: {
        alertMsg,
        verifyCode
    },
    data () {
        return {
            alertOn: false,
            tipText: '确定提交机动车的联系方式吗？'
        };
    },
    computed: {
        ...mapState('driverLicense', {
            driverLicenseUserInfo: 'driverLicenseUserInfo'
        }),
        ...mapState('common', {
            identifyCard: 'identifyCard'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('driverLicense', ['setDriverLicenseBusinessInfo']),

        checkNeedVerify () {
            return checkNeedVerify(this.CONSTANT.location);
        },
        /**
			 * 检验失败提示错误信息，成功弹出确认弹框
			 */
        async submission () {
            if (this.$refs.verifyCode.submission()) {
                this.tipText = '确定提交机动车的联系方式' + this.$refs.verifyCode.item.phoneNumber + '吗？';
                if (this.checkNeedVerify()) {
                    this.alertOn = true;
                } else {
                    const res = await driverLicenseVerifyCode({
                        phone: this.$refs.verifyCode.item.phoneNumber,
                        code: this.$refs.verifyCode.item.verifyCode
                    });
                    if (res && res.code === '0') {
                        this.alertOn = true;
                    } else if (res) {
                        this.changeTipPage({ message: res.message, enableBack: true });
                    }
                }
            }
        },
        /**
			 * 确认提交信息
			 */
        async sure () {
            const res = await driverLicenseSubModify({
                idNum: this.identifyCard.certNumber,
                mobile: this.$refs.verifyCode.item.phoneNumber,
                email: this.driverLicenseUserInfo.email,
                zipCode: this.driverLicenseUserInfo.zipCode,
                address: this.driverLicenseUserInfo.address
            });
            if (res && res.code === '0') {
                this.setDriverLicenseBusinessInfo(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        /**
			 * 取消隐藏提示弹框
			 */
        cancel () {
            this.alertOn = false;
        },
        getVerifyCode () {
            driverLicenseGetVerifyCode({
                phone: this.$refs.verifyCode.item.phoneNumber
            });
        },
        goUp () {
            this.changeStep('up');
        }
    }
};
</script>

<style lang="scss" scoped>
	.wrap-info {
		display: flex;
		justify-content: center;
		align-items: center;
	}
</style>
