<template>
    <div>
        <div class="kd-title-content-wrapper wrap-info">
            <text-table :data="driverLicenseUserInfo"/>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-bianji2"
                text="修改"
                :button-handle="nextStep"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import textTable from '@/components/text-table';
import { driverLicenseeGetUserInfos } from '@/service/traffic/traffic';
import audioCar from '@/assets/media/trafic/300.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'DriverContactTextInfo',
    components: { textTable },
    computed: {
        ...mapState('driverLicense', {
            driverLicenseUserInfo: 'driverLicenseUserInfo',
            text: 'driverLicenseInstructions'
        })
    },
    props: {
        chip: { type: String, default: '' }
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
        this.initData();
    },
    beforeDestroy () {
        this.audioCar.destroy();
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('driverLicense', ['setDriverLicenseUserInfo']),
        preStep () {
            this.changeStep('up');
        },
        nextStep () {
            this.changeStep('down');
        },
        async initData () {
            const res = await driverLicenseeGetUserInfos({ zxbh: this.chip });
            if (res && res.code === '0') {
                this.setDriverLicenseUserInfo(res.result);
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
</style>
