<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
		<input-driver-chip :type="1" v-if="currentStep === steps.findIndex(step=>step.key === 'inputDriverChip')" :nextFunction="getDriverChip"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
		<text-info v-if="currentStep === steps.findIndex(step=>step.key === 'textInfo')" :chip="chip"/>
        <check-phone v-if="currentStep === steps.findIndex(step=>step.key === 'checkPhone')"/>
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="driverLicenseBusinessInfo"
            page-title="变更完成"
            :foot-texts="footTexts"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import serverInfo from '@/components/server-info';
import textInfo from './components/text-info';
import checkPhone from './components/check-phone';
import complete from '@/components/complete';
import process from '@/components/process';
import inputDriverChip from '@/view/pageComponents/input-driver-chip';
import { getDriverLicenseId, driverLicenseFaceRecognition } from '@/service/traffic/traffic';
import { LOCATION_ID } from '@/constant/constant';
import { DRIVER_LICENSE_CHANGE, DRIVER_LICENSE_CHANGE_YINCHUAN } from '@/constant/stepsOption';

export default {
    name: 'DriverContactIndex',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        serverInfo,
        textInfo,
        checkPhone,
        complete,
        process,
        inputDriverChip
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera'
        }),
        ...mapState('driverLicense', {
            driverLicenseBusinessInfo: 'driverLicenseBusinessInfo'
        })
    },
    data () {
        return {
            footTexts: '温馨提示：您的联系方式已变更成功',
            chip: ''
        };
    },
    methods: {
        ...mapMutations('driverLicense', ['setDriverLicenseInstructions']),
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        getDriverChip (chip) {
            this.chip = chip;
            this.changeStep('down');
        },
        async handleBusiness () {
            const res = await getDriverLicenseId({
                zxbh: this.chip,
                idNum: this.identifyCard.certNumber,
                name: this.identifyCard.name
            });
            if (res && res.code === '0') {
                if (res.result.status === 1) {
                    this.handleContrast();
                } else if (res.result.status === 2) {
                    this.changeTipPage({
                        message: '对不起，本地驾驶证资料库内未找到您的驾驶证信息！',
                        imgName: 'imgDriverLocal'
                    });
                } else {
                    this.changeTipPage({ message: res.message });
                }
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleContrast () {
            const res = await driverLicenseFaceRecognition({
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.setDriverLicenseInstructions(res.result.content);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    },
    mounted () {
        if (this.CONSTANT.location === LOCATION_ID.yinchuan || this.CONSTANT.location === LOCATION_ID.zhongwei) {
            this.setSteps(DRIVER_LICENSE_CHANGE_YINCHUAN);
        } else {
            this.setSteps(DRIVER_LICENSE_CHANGE);
        }
    }
};
</script>
